import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'nick-post',
  template: `
    <div class = "col-md-2"></div>
    <div class=" col-md-6 main">

    <h1>{{post.posts[id].title}}</h1>

    <div id="myCarousel" class="carousel slide" >
    
    <div class="carousel-inner" role="listbox">
    <div class = "item active">
      <img [src]="post.posts[id].thumbnail">
      <p>{{post.posts[id].blerb}}</p>
    </div>
    <div *ngFor="let card of post.posts[id].cards" class="item">
      <img [src]="card.img" >
      <p>{{card.info}}</p>
    </div>
    </div>
    
    <a class="left carousel-control" href="#myCarousel" role="button" data-slide="prev">
      <span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
      <span class="sr-only">Previous</span>
    </a>
    <a class="right carousel-control" href="#myCarousel" role="button" data-slide="next">
      <span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
      <span class="sr-only">Next</span>
    </a>
    
    </div>
    <div class="well well-lg">
      <p *ngFor="let paragraph of post.posts[id].paragraphs">{{paragraph}}</p>
    </div>
    </div>


    


    
  `,
})
export class NickPostComponent  {
    id:string;
    
    post=require('NicksPosts/posts.json');
  

    constructor(private route: ActivatedRoute) {
        this.id = this.route.snapshot.params['id'];
        console.log(this.id);
        
    }

    isActive(img:string){
      return img==this.post.posts[this.id].cards[1].img


    }


}


